<template>
  <div class="header-nav">
    <div class="logo-container">
      <div class="logo">绘画艺术</div>
      <div class="slogan">AI作画 · 灵感创作</div>
    </div>
    <div class="nav-menu">
      <el-menu mode="horizontal" :router="true" :default-active="activeIndex" class="nav-list">
        <el-menu-item index="/home">首页</el-menu-item>
        <el-menu-item index="/create">AI绘画创作</el-menu-item>
        <el-menu-item index="/paintings">画作展示</el-menu-item>
        <el-menu-item index="/about">关于我们</el-menu-item>
        <el-menu-item index="/contact">联系我们</el-menu-item>
      </el-menu>
    </div>
    <div class="user-actions">
      <div class="credits-info" v-if="isLoggedIn">
        <i class="el-icon-coin"></i>
        <span class="credits-text">{{ userCredits }} 积分</span>
        <el-button type="text" class="get-more-btn">获取更多</el-button>
      </div>
      <el-dropdown v-if="isLoggedIn" trigger="click" @command="handleCommand">
        <span class="user-info">
          <el-avatar size="medium" icon="el-icon-user"></el-avatar>
          <span class="username">{{ userPhone | formatPhone }}</span>
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item command="profile">个人中心</el-dropdown-item>
          <el-dropdown-item command="works">我的作品</el-dropdown-item>
          <el-dropdown-item command="vip">开通VIP</el-dropdown-item>
          <el-dropdown-item divided command="logout">退出登录</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>
      <div v-else>
        <el-button type="text" @click="goToLogin">登录</el-button> / 
        <el-button type="text" @click="goToRegister">注册</el-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HeaderNav',
  data() {
    return {
      activeIndex: this.$route.path,
      userCredits: 100 // 模拟用户积分
    }
  },
  computed: {
    isLoggedIn() {
      return sessionStorage.getItem('isAuthenticated') === 'true'
    },
    userPhone() {
      return sessionStorage.getItem('userPhone') || ''
    }
  },
  filters: {
    formatPhone(phone) {
      if (!phone) return ''
      return phone.substring(0, 3) + '****' + phone.substring(7)
    }
  },
  methods: {
    handleCommand(command) {
      if (command === 'profile') {
        this.$router.push('/user')
      } else if (command === 'logout') {
        this.logout()
      } else if (command === 'works') {
        this.$router.push('/paintings')
      } else if (command === 'vip') {
        // 打开VIP开通对话框
        this.$message.info('VIP开通功能开发中')
      }
    },
    logout() {
      sessionStorage.removeItem('isAuthenticated')
      sessionStorage.removeItem('userPhone')
      this.$message.success('已退出登录')
      this.$router.push('/login')
    },
    goToLogin() {
      this.$router.push('/login')
    },
    goToRegister() {
      this.$router.push('/register')
    }
  },
  watch: {
    '$route.path'(newPath) {
      this.activeIndex = newPath
    }
  }
}
</script>

